.cards-section {
  &--white {
    background: #ffffff;
  }
}

.cards-section__container {
  padding-top: 80px;
  padding-bottom: 80px;

  @media (max-width: $mobile-width) {
    padding-top: 48px;
    padding-bottom: 48px;
  }
}

.cards-section__title {
  margin: 0;
  margin-bottom: 16px;
}

.cards-section__desc {
  color: $color-text-secondary;
  margin-bottom: 32px;

  @media (max-width: $mobile-width) {
    font-size: 14px;
    line-height: 18px;
    margin-bottom: 39px;
    color: #000000;
  }

  p {
    margin: 0;
  }

  p + p {
    margin-top: 24px;
  }
}

.cards-section__card {
  max-width: 320px;

  @media (max-width: $mobile-width) {
    max-width: 238px;
  }
}

.cards-section__slider,
.webinars__slider {
  @media (max-width: $mobile-width) {
    margin-right: -16px;

    .slider-controls {
      display: none;
    }
  }
}

.cards-section__slider-inner {
  margin-bottom: 32px;

  @media (max-width: $mobile-width) {
    margin-bottom: 0;
  }
}
