.inform-hero {
  background: linear-gradient(339.56deg, #FFFFFF 26.75%, #DCEBF8 82.21%), linear-gradient(179.97deg, rgba(229, 243, 255, 0.7) 0.06%, rgba(255, 255, 255, 0) 156.54%);
  text-align: center;
  overflow: hidden;
  position: relative;

  &::after {
    content: "";
    position: absolute;
    z-index: 2;
    width: 100%;
    height: 115px;
    left: 0;
    bottom: 0;
    right: 0;
    background: url(../img/inform-center/hero-bottom.svg);
    background-size: 100% 204px;
    pointer-events: none;
  }

  @media (max-width: $mobile-width) {
    background: #ffffff;
    padding-top: 56px;

    &::before {
      content: "";
      display: block;
      height: 167px;
      background: url(../img/inform-center/hero-bottom-mobile.svg) no-repeat, url(../img/inform-center/hero-mobile.png) no-repeat;
      background-size: 100% 97px, 320px 160px;
      background-position: left 0 bottom -73px, center bottom;
      background-color: #e6f3ff;
    }

    &::after,
    .to-bottom {
      display: none;
    }
  }
}

.inform-hero__container {
  padding-top: 192px;
  padding-bottom: 95px;
  position: relative;
  z-index: 1;
  min-height: 655px;
  display: flex;
  flex-direction: column;

  &::before,
  &::after {
    content: "";
    position: absolute;
    z-index: -1;
    width: 479px;
    height: 413px;
    left: -215px;
    bottom: 55px;
    background: url(../img/inform-center/hero-left.png);
    background-size: 100% 100%;
  }

  &::after {
    width: 333px;
    height: 530px;
    background-image: url(../img/inform-center/hero-right.png);
    left: auto;
    right: -122px;
    bottom: -70px;
  }

  @media (max-width: $mobile-width) {
    padding-top: 34px;
    padding-bottom: 48px;
    min-height: auto;

    &::before,
    &::after {
      display: none;
    }
  }
}

.inform-hero__title {
  margin: 0;
  margin-bottom: 20px;

  opacity: 0;

  &--show {
    opacity: 1;
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 16px;
  }
}

.inform-hero__desc {
  font-size: 18px;
  line-height: 31px;
  max-width: 620px;
  margin: 0 auto 25px;

  p {
    margin: 0;
  }

  p + p {
    margin-top: 24px;
  }

  @media (max-width: $mobile-width) {
    color: #656565;

    font-size: 14px;
    line-height: 22px;

    margin-bottom: 34px;
  }
}

.inform-hero__actions {
  margin-bottom: 130px;

  @media (max-width: $mobile-width) {
    margin-bottom: 0;
  }
}
