.article {
  font-size: 16px;
  line-height: 23px;

  & > * {
    margin: 0;
    margin-bottom: 24px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  h1 {
    font-weight: 600;
    font-size: 24px;
    line-height: 34px;
    margin-bottom: 35px;
  }

  h2 {
    font-weight: 600;
    font-size: 21px;
    line-height: 34px;
    margin-top: 40px;
  }

  h3 {
    margin-top: 40px;
    font-size: 18px;
    line-height: 25px;
  }

  h2 + h3 {
    margin-top: 0;
  }

  figure {
    margin: 0;
  }

  figcaption {
    font-size: 13px;
    text-align: center;
    color: $color-text-secondary;
    margin-top: 5px;
  }

  cite {
    display: block;
  }

  a {
    @include multilineUnderline($color-main);
    color: $color-main;

    &::after {
      opacity: 1;
    }

    &:hover::after {
      opacity: 0;
    }
  }

  @media (max-width: $mobile-width) {
    h1 {
      margin-bottom: 24px;
    }
  }
}

.article__figures {
  position: relative;

  &:hover {
    .article__pagination,
    .article__btn:not(.swiper-button-disabled) {
      opacity: 1;
    }
  }

  .swiper-slide {
    width: 100%;
  }
}

.article__pagination {
  background-color: rgba(0, 0, 0, .4);
  border-radius: 6px;
  color: #ffffff;
  position: absolute;
  z-index: 3;
  top: 12px;
  right: 12px;
  padding: 8px 12px;

  left: auto;
  bottom: auto;
  width: auto;

  font-size: 0;
  line-height: 0;
  display: flex;
  align-items: baseline;
  opacity: 0;
  transition: opacity 0.3s;

  &::after,
  span {
    font-size: 13px;
    line-height: 13px;
  }

  .swiper-pagination-current {
    order: 1;
  }

  &::after {
    content: "из";
    order: 2;
    display: block;
    margin: 0 4px;
  }

  .swiper-pagination-total {
    order: 3;
  }
}

