.info-card {
  border: 1px solid rgba(0, 0, 0, 0.05);
  border-radius: 8px;
  overflow: hidden;

  display: block;
  text-decoration: none;
  color: inherit;
  cursor: pointer;

  // &:hover {
  //   .info-card__title {
  //     color: $color-main;
  //   }
  // }
}

.info-card__text {
  background: #ffffff;
  padding: 16px;

  @media (max-width: $mobile-width) {
    padding-bottom: 12px;
  }
}

.info-card__title {
  margin: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;

  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  min-height: 42px;
  letter-spacing: 0.3px;
  transition: color 0.3s;
}

.info-card__image {
  height: 206px;

  display: flex;
  align-items: center;
  justify-content: center;
  background: #eeeeee;

  position: relative;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &--y-clients {
    background: #f9f4e7;
  }

  &--cerebro {
    background: #fdf8e3;
  }

  &--amplifer {
    background: #f1eaff;
  }

  &--targethunter {
    background: #ebf1f5;
  }

  &--ecwid {
    background: #deeff9;
  }

  &--elama {
    background: #eaf5f8;
  }

  &--geekbrains {
    background: #d0d4fd;
  }

  &--bitrix24 {
    background: #d0f2fd;
  }

  &--amocrm {
    background: #daf0f8;
  }

  &--eresh {
    background: #e3daf8;
  }

  &--bothelp {
    background: #f8dcda;
  }

  &--r-broker {
    background: #d0f5fd;
  }

  &--calltouch {
    background: #d0f5fd;
  }

  &--spycat {
    background: #dae9f8;
  }

  &--albato {
    background: #f8e6da;
  }

  &--logo {
    img {
      max-width: 200px;
      width: auto;
      height: auto;
      object-fit: fill;
    }
  }

  @media (max-width: $mobile-width) {
    height: 134px;
  }
}

.info-card__aside {
  position: absolute;
  z-index: 1;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(34, 34, 34, 0.6);

  font-size: 13px;
  line-height: 17px;
  color: #ffffff;

  padding: 3px 16px;
}

.info-card__link {
  margin-top: 6px;

  display: inline-block;
  vertical-align: top;

  font-size: 13px;
  line-height: 17px;

  color: $color-text-secondary;

  transition: color 0.5s;

  &:hover,
  &:focus {
    color: $color-main;
  }
}
