.btn-icon {
  display: inline-flex;
  align-items: center;
  background: $color-main;
  padding: 16px 32px;
  font-size: 18px;
  line-height: 20px;
  color: #ffffff;
  border-radius: 52px;
  transition: opacity 0.5s;

  &::after {
    content: "";
    display: block;
    background: url("data:image/svg+xml;charset=utf-8,%3Csvg width='6' height='10' viewBox='0 0 6 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 1l4 4-4 4' stroke='%23FFF' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
    width: 6px;
    height: 10px;
    margin-left: 8px;
  }

  &:hover,
  &:focus {
    opacity: 0.8;
  }
}

.btn {
  display: inline-block;
  vertical-align: top;
  font-size: 18px;
  line-height: 21px;
  color: $color-main;
  transition: opacity 0.5s;

  &:hover,
  &:focus {
    opacity: 0.8;
  }

  @media (max-width: $mobile-width) {
    font-size: 16px;
    line-height: 19px;
  }
}
