html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  min-width: $width-min;

  margin: 0;
  padding: 0;

  font-family: $font-family;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  color: $color-text;
  background: #f9f9fa;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: top;
}

.container {
  width: 100%;
  max-width: 1060px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 30px;
  padding-right: 30px;

  @media (max-width: $mobile-width) {
    padding-left: 16px;
    padding-right: 16px;
  }
}

a {
  text-decoration: none;
}

.h1 {
  @include h1;
}

.h2 {
  @include h2;
}

.to-bottom {
  @include btn-reset;
  display: block;
  width: 36px;
  height: 20px;
  margin: auto;
  margin-bottom: 0;
  cursor: pointer;

  svg {
    stroke: #ecf3f9;
  }

  &:focus {
    outline: none;
  }
}

header.inform-header.nd-vkbiz-header--white,
header.inform-header.nd-vkbiz-header--white .nd-vkbiz-header__nav {
  transition: none;
}
