.slider-controls {
  display: flex;
  align-items: center;
}

.slider-controls__btn {
  @include btn-reset;
  cursor: pointer;
  width: 34px;
  height: 34px;
  border: 1px solid #c1c9d1;
  border-radius: 50%;
  transition: background-color 0.3s, border-color 0.3s;
  background-color: #ffffff;

  svg {
    stroke: #222222;
  }

  &:not(.swiper-button-disabled):hover,
  &:not(.swiper-button-disabled):focus {
    background-color: $color-main;
    border-color: transparent;
    outline: none;

    svg {
      stroke: #ffffff;
    }
  }

  &.swiper-button-disabled {
    border-color: #dde1e5;
    outline: none;

    svg {
      stroke: #c1c9d1;
    }
  }
}

.slider-controls__pagination {
  display: flex;
  align-items: center;
  padding: 14px 20px;
}

.slider-controls .swiper-pagination-bullet {
  width: 6px;
  height: 6px;
  background: #c1c9d1;
  opacity: 1;
  margin-right: 8px;

  &:last-child {
    margin-right: 0;
  }
}

.slider-controls .swiper-pagination-bullet-active {
  background: #4680c2;
}
