.courses-banner {
  background: linear-gradient(75.43deg, #f4f8fa 0.45%, #d0e0ee 98.35%);
  border-radius: 14px;
  min-height: 300px;
  padding: 40px;
  position: relative;
  overflow: hidden;

  @media (max-width: 879px) {
    min-height: 421px;
    padding: 24px 20px;
  }
}

.courses-banner__title {
  line-height: 47px;
  font-size: 36px;
  width: 500px;
  margin: 0;
  font-family: $font-commons;
  font-weight: 500;

  @media (max-width: 879px) {
    width: 500px;
    line-height: 29px;
    font-size: 22px;
    max-width: 248px;
  }
}

.courses-banner__img {
  background: url("../img/biz/courses/ads_biz_banner.png") 50% no-repeat;
  background-size: cover;
  width: 376px;
  height: 287px;

  @media (min-width: 880px) {
    position: absolute;
    bottom: -27px;
    right: 73px;
  }

  @media (max-width: 879px) {
    width: 248px;
    height: 219px;
    margin: auto;
    background: url("../img/biz/courses/ads_biz_banner_mobile.png") no-repeat;
    background-size: cover;
  }
}

.is_2x .courses-banner__img {
  background-image: url("../img/biz/courses/ads_biz_banner_2x.png");

  @media (max-width: 879px) {
    background-image: url("../img/biz/courses/ads_biz_banner_mobile_2x.png");
  }
}

.courses-banner__free {
  line-height: 17px;
  font-size: 12px;
  letter-spacing: 0.1em;
  color: #707c87;
  text-transform: uppercase;
  margin-top: 8px;

  @media (max-width: 879px) {
    margin-top: 8px;
    margin-bottom: 15px;
  }
}

.courses-banner__goto {
  display: inline-flex;
  vertical-align: top;
  align-items: center;
  min-width: 185px;
  height: 46px;
  background-color: #ffffff;
  transition: background-color 0.25s ease;
  border-radius: 23px;
  line-height: 46px;
  font-size: 18px;
  text-align: center;
  color: #4680c2;
  padding: 0 32px;
  margin-top: 50px;
  justify-content: center;

  &:hover {
    background-color: hsla(0, 0%, 100%, 0.8);
    transition: background-color 0.25s ease;
    text-decoration: none;
  }

  &:active {
    background-color: hsla(0, 0%, 100%, 0.8)
  }

  &::after {
    display: inline-block;
    content: "";
    height: 10px;
    width: 6px;
    background: url("data:image/svg+xml;charset=utf-8,%3Csvg%20viewBox%3D%220%200%206%2010%22%20fill%3D%22none%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%0A%20%20%20%20%3Cpath%20d%3D%22M1%201L5%205L1%209%22%20stroke%3D%22%234680C2%22%20stroke-width%3D%221.5%22%20stroke-linecap%3D%22round%22%20stroke-linejoin%3D%22round%22%2F%3E%0A%3C%2Fsvg%3E") 50% no-repeat;
    line-height: 46px;
    margin-left: 8px;
  }

  @media (max-width: 879px) {
    display: flex;
    margin: 0;
    margin-top: 10px;
    text-decoration: none;
  }
}
