.partner-card {

}

.partner-card__head {
  display: flex;
  align-items: center;

  margin-bottom: 25px;

  @media (max-width: $mobile-width) {
    display: block;
  }
}

.partner-card__logo {
  width: 80px;
  height: 80px;

  display: flex;
  align-items: center;
  justify-self: center;

  margin-right: 20px;
  flex-shrink: 0;

  @media (max-width: $mobile-width) {
    margin: 0 auto 24px;
  }
}

.partner-card__title {
  font-weight: 600;
  font-size: 24px;
  line-height: 31px;
  margin: 0;
}

.partner-card__text,
.partner-card__secondary {
  font-size: 16px;
  line-height: 22px;
  margin-bottom: 22px;

  a {
    @include multilineUnderline($color-main);

    color: $color-main;

    &::after {
      opacity: 1;
    }

    &:hover::after {
      opacity: 0;
    }
  }

  p {
    margin: 0;
  }

  p + p {
    margin-top: 24px;
  }
}

.partner-card__secondary {
  color: $color-text-secondary;
  margin-bottom: 0;
}

.partner-card__link {
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: $color-main;
  display: inline-flex;
  vertical-align: top;
  align-items: center;
  transition: opacity 0.3s;

  &::before {
    content: "";
    display: block;
    width: 24px;
    height: 24px;
    background: url("../img/inform-center/icon-anchor.svg") no-repeat;
    background-size: 100% 100%;
    margin-right: 8px;
  }

  &:hover,
  &:focus {
    opacity: 0.8;
  }
}
