.content-popup {
  position: fixed;
  z-index: 9999;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.6);
  overflow: auto;

  display: none;
  cursor: pointer;

  &--show {
    display: block;
  }

  @media (max-width: $mobile-width) {
    top: 56px;
  }
}

.content-popup__inner {
  padding: 90px;

  display: flex;
  flex-direction: column;
  min-height: 100vh;

  @media (max-width: $mobile-width) {
    padding: 0;
    min-height: 100%;
    display: block;
  }
}

.content-popup__wrap {
  background: #ffffff;
  border-radius: 6px;

  padding: 40px 50px;
  overflow: hidden;

  position: relative;
  width: 100%;
  max-width: 810px;
  margin: auto;
  cursor: auto;

  @media (max-width: $mobile-width) {
    border-radius: 0;
    padding: 16px;
    margin: 0;
    min-height: calc((var(--vh, 1vh) * 100) - 56px);
  }
}

.content-popup__close {
  @include btn-reset;
  width: 30px;
  height: 30px;

  display: flex;
  align-items: center;
  justify-content: center;

  position: absolute;
  z-index: 1;
  right: 24px;
  top: 20px;
  opacity: 0.3;
  transition: opacity 0.3s;

  svg {
    display: block;
  }

  &:hover,
  &:focus {
    opacity: 1;
    outline: none;
  }

  @media (max-width: $mobile-width) {
    position: static;
    width: auto;
    height: auto;
    opacity: 1;
    color: $color-main;
    margin-bottom: 24px;

    &:last-child {
      margin-top: 24px;
      margin-bottom: 0;
    }

    svg {
      display: none;
    }
  }
}

.content-popup--small {
  .content-popup__wrap {
    padding: 32px;
    max-width: 700px;

    @media (max-width: $mobile-width) {
      padding: 16px;
      max-width: 100%;
    }
  }
}

.content-popup__mobile-text {
  display: none;

  @media (max-width: $mobile-width) {
    display: inline-flex;
    align-items: center;

    &::before {
      content: "";
      display: block;
      width: 6px;
      height: 10px;
      background: url(../img/inform-center/icon-arrow-left.svg) no-repeat;
      margin-right: 8px;
    }
  }
}
